<template>
  <div class="spread-sheet-view">
    <div class="spread-sheet-view__control-top">
      <div class="item item_btn">
        <v-btn small dark color="blue darken-3" @click="createNewDocument">
          <v-icon small left>mdi-file-table-outline</v-icon> </v-btn>
      </div>
      <v-form class="form-file" ref="FormFile">
        <div class="item item_btn item_file">
          <v-file-input dense
                        truncate-length="16"
                        label=" "
                        v-model="fileTemplate"
                        :disabled="isFileTemplateDisabled"
                        @change="openJSONFileTemplate">
          </v-file-input>
        </div>
        <div class="item item_btn item_file">
          <v-file-input dense
                        truncate-length="16"
                        label=" "
                        v-model="fileSetting"
                        :disabled="isFileSettingDisabled"
                        @change="openJSONFileSetting">
          </v-file-input>
        </div>
        <div class="item item_btn item_file">
          <v-file-input dense
                        truncate-length="16"
                        label=" "
                        v-model="fileData"
                        :disabled="isFileDataDisabled"
                        @change="openJSONFileData">
          </v-file-input>
        </div>
      </v-form>

      <div class="item item_btn">
        <v-menu bottom
                :offset-y="true">
          <template v-slot:activator="{ on, attrs }">
            <v-btn small dark color="blue darken-3" v-bind="attrs" v-on="on">
              <v-icon small left>mdi-cloud-download-outline</v-icon></v-btn>
          </template>
          <v-list>
            <v-list-item dense link @click="saveDocument">
              <v-list-item-title> </v-list-item-title>
            </v-list-item>
            <v-list-item dense link @click="saveDocumentData">
              <v-list-item-title> </v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
      </div>
      <div class="item item_btn">
        <v-btn small dark color="blue darken-3" @click="openPrintPage">
          <v-icon small left>mdi-cloud-print-outline</v-icon></v-btn>
      </div>
      <div class="item item_btn">
        <v-menu bottom
                :offset-y="true"
                min-width="200">
          <template v-slot:activator="{ on, attrs }">
            <v-btn small dark color="blue darken-3" v-bind="attrs" v-on="on">
              <v-icon small left>mdi-tune-vertical</v-icon></v-btn>
          </template>
          <v-list>
            <v-list-item>
              <v-checkbox dense
                          label=""
                          v-model="isGrid"
                          @input="isGrid = !isGrid"></v-checkbox>
            </v-list-item>
            <v-list-item>
              <v-checkbox dense
                          label=""
                          v-model="isTitle"
                          @input="isTitle = !isTitle"></v-checkbox>
            </v-list-item>
          </v-list>
        </v-menu>
      </div>
      <!-- <div class="item item_btn">
        <v-btn small dark color="blue darken-3" @click="insertColumn">
          <v-icon small left>mdi-cloud-print-outline</v-icon>Test button</v-btn>
      </div> -->
      <dialog-modal is-dialog-name=""
                    :opacity="0"
                    :is-dialog-show="isDialogMessageShow"
                    :is-control="true"
                    :is-accept="{ isShow: false }"
                    :is-cancel="{ isShow: true, text: '' }"
                    @dialog:cancel="closeDialogMessage">
        <span class="dialog-message">{{ isDialogMessageText }}</span>
      </dialog-modal>
    </div>
    <div class="spread-sheet-view__table">
      <spread-sheet-editing ref="SpreadSheetEditDOM"
                            :cell="editableCell"
                            :cell-type="editableCellType"
                            :cell-element="editableCellElement"
                            :edit-event="editableCellEvent"
                            :is-label="isShowEditableLabel"
                            @editing:accept="acceptEditingCell"
                            @editing:cancel="cancelEditingCell"></spread-sheet-editing>
      <spread-sheet ref="SpreadSheet"
                    v-bind="tableDocument"
                    :is-show-grid="isGrid"
                    :is-show-title="isTitle"
                    @click:cell="evtClickCell"
                    @dblclick:cell="evtDblClickCell"
                    @keydown:cell="evtKeydownCell"
                    @buffer:copy="evtBufferCopy"
                    @buffer:paste="evtBufferPaste"
                    @scroll:body="scrollBody"></spread-sheet>
    </div>
    <v-snackbar content-class="snack"
                color="red darken-3"
                v-model="isSnackMessage">{{ snackMessage }}</v-snackbar>
  </div>
</template>

<script>
import SpreadSheet from '@/components/SpreadSheet/SpreadSheet.vue';
import SpreadSheetEditing from '@/components/SpreadSheetEditing/SpreadSheetEditing.vue';

import DialogModal from '@/components/Dialogs/DialogModal.vue';

import apiSpreadSheet from '@/plugins/apiSpreadSheet/apiSpreadSheet';
import TableDocument from '@/components/TableDocument/TableDocument';
import TableDocumentApi from '@/components/TableDocument/TableDocumentApi';
import Buffer from '@/plugins/copyAndPaste';

import TableDocumentGeneralError from '../components/TableDocument/TableDocumentGeneralError';

export default {
  name: 'SpreadSheetView',
  components: {
    SpreadSheet,
    SpreadSheetEditing,
    DialogModal,
  },
  data() {
    return {
      tableDocument: new TableDocument(),
      tableDocumentPrepare: new TableDocumentApi(),
      fileTemplate: [],
      fileSetting: [],
      fileData: [],

      editableCell: null,
      editableCellType: null,
      editableCellEvent: null,
      editableCellElement: null,
      editableCellGeometry: null,
      isShowEditableLabel: false,

      isGrid: true,
      isTitle: true,
      // isPrintMode: false,
      isFileTemplateDisabled: false,
      isFileDataDisabled: true,
      isFileSettingDisabled: true,

      isDialogMessageShow: false,
      isDialogMessageText: '',

      isSnackTimer: null,
      isSnackMessage: false,
      snackMessage: '',

      rangeCopy: null,
    };
  },
  mounted() {
    // console.log(this.tableDocument);
  },
  methods: {
    evtBufferCopy(cellName) {
      console.log(this.tableDocument.getCell(cellName).value);
      Buffer.copy(this.tableDocument.getCell(cellName).value);
    },
    async evtBufferPaste(cellName) {
      if (!this.tableDocument.hasEditing(cellName)) {
        return;
      }
      const pasteValue = await Buffer.paste();
      // console.log(pasteValue);
      try {
        this.tableDocument.editingCell(cellName, pasteValue);
      } catch (err) {
        // console.log(err);
        this.showDialogMessage(err.getMessagesText());
        // this.showSnackMessage(err.getMessagesText());
      }
    },
    saveDocument() {
      const JSONFormat = true;
      apiSpreadSheet.dowloadJSONFile(JSON.stringify(this.tableDocument.getDocument()), JSONFormat);
    },
    saveDocumentData() {
      const JSONFormat = true;
      apiSpreadSheet.dowloadJSONFile(JSON.stringify(this.tableDocument.serializationDataSection()), JSONFormat);
    },
    evtClickCell(options) {
      const { cellName } = options;
      if (cellName) {
        try {
          this.tableDocument.actionCell(cellName);
        } catch (err) {
          this.showDialogMessage(err.getMessagesText());
        }
      }
      // const selectedCell = this.tableDocument.getCell(cellName);
      // if (Object.keys(selectedCell).includes('action')) this.tableDocument.executeAction(cellName);
      // if (Object.keys(selectedCell).includes('formula')) this.tableDocument.calculateCellValue(cellName);
    },

    evtDblClickCell(options) {
      const { cellName } = options;
      if (!this.tableDocument.hasEditing(cellName)) {
        // this.showSnackMessage(' ');
        return;
      }
      this.editableCell = this.tableDocument.getCell(cellName);
      this.editableCellType = this.tableDocument.getCellType(cellName);
      this.editableCellEvent = options.evt;
      this.editableCellElement = options.evt.target;
      this.editableCellGeometry = options.evt.target.getBoundingClientRect();
      this.$nextTick().then(() => {
        this.$refs.SpreadSheetEditDOM.$el.focus();
      });
    },

    showSnackMessage(message) {
      clearTimeout(this.isSnackTimer);
      this.snackMessage = message;
      this.isSnackMessage = true;
      this.isSnackTimer = setTimeout(() => {
        this.isSnackMessage = false;
        this.snackMessage = '';
      }, 2000);
    },

    showDialogMessage(message) {
      this.isDialogMessageShow = true;
      this.isDialogMessageText = message;
    },
    closeDialogMessage() {
      this.isDialogMessageShow = false;
      this.isDialogMessageText = null;
    },
    evtKeydownCell(options) {
      this.evtDblClickCell(options);
    },

    clearEditCell() {
      // console.log(this.editableCellElement);
      if (this.editableCellElement) this.editableCellElement.focus();
      this.editableCell = null;
      this.editableCellType = null;
      this.editableCellEvent = null;
      this.editableCellElement = null;
      this.isShowEditableLabel = false;
    },
    scrollBody() { //    ???
      if (this.editableCellElement) {
        const cellName = this.editableCellElement.getAttribute('data-name');
        const cellEdit = this.$refs.SpreadSheet.$el.querySelector(`[data-name="${cellName}"]`).getBoundingClientRect();
        if ((this.editableCellGeometry.left > cellEdit.left + 10
          || this.editableCellGeometry.left < cellEdit.left - 10)
          || (this.editableCellGeometry.top > cellEdit.top
          || this.editableCellGeometry.top < cellEdit.top)) {
          this.isShowEditableLabel = true;
        } else {
          this.isShowEditableLabel = false;
        }
      } else {
        this.isShowEditableLabel = false;
      }
    },
    acceptEditingCell(option) {
      try {
        this.tableDocument.editingCell(option.cellName, option.value);
      } catch (err) {
        console.log(err);
        // this.showSnackMessage(err.getMessagesText());
        this.showDialogMessage(err.getMessagesText());
      } finally {
        this.clearEditCell();
      }
    },
    cancelEditingCell() {
      this.clearEditCell();
    },

    async createNewDocument() {
      this.$refs.SpreadSheet.newDocument();
      await this.$nextTick();
      this.tableDocument = new TableDocument();
      this.tableDocumentPrepare = new TableDocumentApi();
      this.isGrid = true;

      this.isFileTemplateDisabled = false;
      this.isFileDataDisabled = true;
      this.isFileSettingDisabled = true;

      this.fileTemplate = undefined;
      this.fileSetting = undefined;
      this.fileData = undefined;
      this.$refs.FormFile.reset();
    },

    openJSONFileTemplate(file) {
      if (file === null || Object.keys(file).includes('length')) return;
      apiSpreadSheet.uploadJSONFile(file).then(async (JSONTemplate) => {
        const { type } = JSON.parse(JSONTemplate);
        if (type === 'document') {
          const temp = await new TableDocumentApi({ JSONString: JSONTemplate });
          this.tableDocument = temp;
          this.isFileTemplateDisabled = true;
          this.isGrid = false;
          return;
        }
        try {
          this.tableDocumentPrepare.setTableDocumentTemplate(JSONTemplate);
        } catch (err) {
          this.showDialogMessage(err.getMessagesText());
          this.fileTemplate = null;
        }
        this.isFileSettingDisabled = false;
      });
    },

    async openJSONFileSetting(file) {
      if (file === null || Object.keys(file).includes('length')) return;
      const JSONSettings = await apiSpreadSheet.uploadJSONFile(file);
      try {
        this.tableDocumentPrepare.setTableDocumentSettings(JSONSettings);
      } catch (err) {
        this.showDialogMessage(err.getMessagesText());
        this.fileSetting = null;
      }
      this.isFileDataDisabled = false;
    },

    async openJSONFileData(file) {
      if (file === null || Object.keys(file).includes('length')) return;
      const JSONData = await apiSpreadSheet.uploadJSONFile(file);
      try {
        await this.tableDocumentPrepare.deserialize(JSONData);
      } catch (err) {
        if (err instanceof TableDocumentGeneralError) {
          this.showDialogMessage(err.getMessagesText());
        }
        console.log(err);
      } finally {
        this.tableDocument = this.tableDocumentPrepare;
        this.tableDocument.recalculateFormulas();
        this.isGrid = false;
      }
    },
    openPrintPage() {
      localStorage.setItem('SpreadSheetTableDocument', this.tableDocument.getDocument(true));
      const pagePrint = this.$router.resolve({
        name: 'SpreadSheetPrint',
      });
      window.open(pagePrint.href, '_blank');
    },
  },
};
</script>

<style lang="scss" scoped>
.spread-sheet-view {
  position: relative;
  display: grid;
  grid-template-areas: "control-top" "table";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 1fr;
  max-width: 100%;
  user-select: none;
  &__control-top {
    grid-area: control-top;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 5px;
    padding-left: 18px;
    z-index: 100;
    .form-file {
      display: flex;
    }
    .item {
      padding-right: 20px;
      &_btn {
        align-self: center;
      }
      &_file {
        width: 240px;
        padding-top: 10px;
      }
    }
  }
  &__table {
    position: relative;
    grid-area: table;
    padding: 5px;
    width: calc(100vw - 0px);
    height: calc(100vh - 126px);
    z-index: 50;
  }
}
.dialog-message {
  white-space: pre-line;
}
@media print {
  .spread-sheet-view {
    grid-template-areas: "table";
    grid-template-rows: 1fr;
    &__control-top {
      display: none;
    }
  }
}
</style>
